main {
  position: relative;
}

.hide {
  display: none;
}

.selected {
  background-color: var(--foreground);
  color: var(--background);
}

.selected .arrow {
  filter: invert(87%) sepia(0%) saturate(2519%) hue-rotate(315deg) brightness(98%) contrast(75%);
}

.controls {
  background-color: var(--background);
  width: 80%;
  min-width: 500px;
  max-width: 750px;
  height: 2rem;
  color: var(--foreground);
  z-index: 10;
  border: 3px solid var(--foreground);
  border-radius: 15px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 20%);
}

#dropdown-content {
  border: 5px solid var(--background);
  background-color: var(--background);
  border-radius: 15px;
  width: 10rem;
  max-height: 85vh;
  position: absolute;
  top: 115%;
  overflow: hidden;
}

#dropdown-content #planet-div {
  justify-content: space-between;
  display: flex;
}

#dropdown-content #moon-div label {
  width: max-content;
  margin: auto;
  display: block;
}

.controls .dropdown {
  width: 10rem;
  margin: auto;
  padding: .3em;
  display: flex;
}

#stop-viewing {
  border: 2px solid var(--foreground);
  background-color: var(--foreground);
  color: var(--background);
  border-radius: 15px;
  margin: auto;
  padding: 2px 1%;
  font-size: 1rem;
}

#stop-viewing:hover {
  background-color: var(--color3);
  color: var(--foreground);
  border-color: var(--color3);
}

.arrow {
  cursor: pointer;
  filter: invert(6%) sepia(1%) saturate(5941%) hue-rotate(202deg) brightness(90%) contrast(101%);
  width: 1.2rem;
  margin: auto 0 auto auto;
  transition: transform .4s ease-in-out;
}

.dropdown p {
  text-align: center;
  height: 100%;
  margin: 0;
}

.dropdown label:hover {
  color: var(--color3);
}

.speed lable {
  margin: auto;
  display: inline-block;
}

.speed {
  margin: auto;
}

#speed-control {
  background: var(--foreground);
  vertical-align: middle;
  -webkit-transition: all .2s;
  border-radius: 15px;
  width: 8rem;
  height: .4rem;
  transition: opacity .2s;
  display: inline;
}

#speed-control::-webkit-slider-thumb {
  appearance: none;
  background: var(--color3);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  width: 1.3rem;
  height: 1.3rem;
}

#speed-control::-moz-range-thumb {
  background: var(--color3);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  width: 1.3rem;
  height: 1.3rem;
}

#solarsystem-canvas {
  z-index: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}
